.c-navbar {
	.rot > .course-name {
		display: none;
	}

	.sandwich {
		margin: 17px 19px;

		div {
			height: 4px;
			width: 25px;
		}
		.black {
			background-color: #5d5d5d;
		}
		.arrow {
			font-size: 2em;
			margin-top: -15px; /*!!!! How make the best??? */
		}
	}

	.header-wrap {
		height: 50px;
		background-color: #fff;
		z-index: 11;
		max-width: 550px;
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
	}

	.header-wrap.debug {
		border: 1px solid black;
	}

	.header-title {
		color: #5d5d5d;
		font-size: 15px;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		height: 100%;
		margin-left: calc(25px + 19px * 2);
	}

	.sidebar-header {
		background-color: #1b166e;
		width: 100%;
		height: 125px;
		display: block;
		text-align: center;
		padding: 16px 0 0 14px;
		box-sizing: border-box;
		position: relative;
	}

	.cross {
		color: white;
		font-size: 2.5em;
		display: flex;
		justify-content: flex-end;
		align-items: flex-start;
		z-index: 1;
		line-height: 0;
		margin-top: 10px;
	}

	.appleseeds-logo {
		width: 75%;
		position: absolute;
		left: 41px;
		right: 0;
		top: 26px;
		bottom: 0;
		margin: auto;
	}

	.sidebar-open .sidebar-wrap {
		width: 283px;
		z-index: 1;
	}

	.profile-image {
		width: 35px;
		height: 35px;
		display: inline-block;
		vertical-align: middle;
		border-radius: 50%;
		border: 4px solid #dbe9f9;
		background-position: center;
		transform: Scale(0, 0);
		-moz-transform: Scale(0, 0);
		-ms-transform: Scale(0, 0);
		-webkit-transform: Scale(0, 0);
		-o-transform: Scale(0, 0);
		transition: transform 0.5s ease;
		-moz-transition: -moz-transform 0.5s ease;
		-ms-transition: -ms-transform 0.5s ease;
		-webkit-transition: -webkit-transform 0.5s ease;
		-o-transition: -o-transform 0.5s ease;
		position: relative;
		z-index: 2;
	}

	.user-name {
		font-size: 24px;
	}

	.profile-preview {
		height: calc(7vh + 4px);
		padding: 1vh;
		border-bottom: 1px solid #ccc;
		display: flex;
		flex-direction: row;
		align-items: center;
		margin-top: 20px;
	}

	.name-wrap {
		margin-right: 12px;
	}

	.menu-item {
		display: flex;
		flex-direction: row;
		height: 50px;
		border-top: solid 1px #665d5d5d;
		padding-right: 20px;
		align-items: center;

		.image {
			width: 29px;
			display: flex;
			justify-content: center;
			align-items: center;
		}

		.text {
			margin-right: 16px;
			font-size: 20px;
		}

		.arrow {
			margin-right: auto;
			margin-left: 16px;
			transform: rotate(270deg);
			animation: close-sub 1s;
		}

		.menu-item.open > .arrow {
			transform: rotate(180deg);
			animation: open-sub 1s;
		}
	}

	.menu-item.sub {
		border-top: none;
	}

	.children {
		height: 0px;
		overflow: hidden;
		animation: close-children 1s;
		border: none;

		.menu-item {
			border: none;
		}
	}

	.children.open {
		height: 150px;
		animation: open-children 1s;
	}

	@keyframes open-sub {
		from {
			transform: rotate(270deg);
		}

		to {
			transform: rotate(180deg);
		}
	}
	@keyframes close-sub {
		from {
			transform: rotate(180deg);
		}
		to {
			transform: rotate(270deg);
		}
	}

	@keyframes open-children {
		from {
			height: 0px;
		}

		to {
			height: 150px;
		}
	}
	@keyframes close-children {
		from {
			height: 150px;
		}
		to {
			height: 0px;
		}
	}

	.sidebar-background {
		background-color: rgba(0, 0, 0, 0.4);
		width: 0;
		height: 0;
		right: 0;
		top: 0;
		position: fixed;
		opacity: 0;

		transition: opacity 0.5s ease;
		-moz-transition: opacity 0.5s ease;
		-ms-transition: opacity 0.5s ease;
		-webkit-transition: opacity 0.5s ease;
		-o-transition: opacity 0.5s ease;
	}

	.sidebar {
		background-color: #fafafa;
		height: 100%;
		width: 100%;
	}

	.sidebar-wrap {
		right: 0;
		position: fixed;
		top: 0px;
		bottom: 0px;
		/* z-index: 1000; */
		height: 100%;
		transition: width 0.5s ease;
		-moz-transition: width 0.5s ease;
		-ms-transition: width 0.5s ease;
		-webkit-transition: width 0.5s ease;
		-o-transition: width 0.5s ease;
		width: 0;
		overflow: hidden;
	}

	.sidebar-open {
		.profile-image {
			transform: Scale(1, 1);
			-moz-transform: Scale(1, 1);
			-ms-transform: Scale(1, 1);
			-webkit-transform: Scale(1, 1);
			-o-transform: Scale(1, 1);
		}

		.sidebar-background {
			opacity: 1;
			width: 100%;
			height: 100%;
		}
	}

	a{
		color: rgb(93, 93, 93);
	}
}
