
.c-course-header {
    width: 100%;
    font-family: 'Assistant-SemiBold';
    box-shadow: 0 3px 6px 0 rgb(0, 0, 0, 0.16);
}

.c-course-header .top {
    display: flex;
    padding-top: 17px;
}

.c-course-header .top h1 {
    font-size: 28px;
    padding-bottom: 36px;
    padding-right: 18px;
    width: 93px;
    padding-left: 200px;
}

.c-course-header h6 {
    font-size: 10px;
    padding-right: 18px;
    padding-bottom: 21px;
}

.top > .icons {
    padding: 0 5px;
}

.icons .back-icon {
    width: 22px;
    height: 16px;
}

.icons .copy-icon {
    width: 19px;
    height: 20px;
}

.icons .save-icon {
    width: 19px;
    height: 20px;
}
